@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  color: rgb(30, 30, 30);
}

/* TOPBARR */
/* ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */
.topbarr-container {
  font-size: 1rem;
  height: 5vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 2rem 0;
}

.topbarr-logo {
  font-weight: bolder;
}

.topbarr-stuf {
  font-weight: bolder;
  display: flex;
  align-items: center;
}

.topbarr-stuf img {
  height: 1rem;
  padding: 0 10px;
}


.menu-btn {
  background-color: rgb(255, 255, 255);
  border: none;
  width: 7rem;
  height: 3rem;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
}

.menu-btn img {
  padding-left: 10px;
  width: 2rem;
  height: 2rem;
}

.menu-btn:hover {
  cursor: pointer;
  background-color: rgb(233, 233, 233);
}

.menu-opts {
  margin-top: 1rem;
  width: 8rem;
  border-radius: 0.5rem;
  position: absolute;
  top: 4rem;
  display: none;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 0px 5px rgb(161, 161, 161);
  flex-direction: column;
}

.menu-opts a {
  padding: 1rem;
  text-decoration: none;
}

.menu-opts a:hover {
  cursor: pointer;
  background-color: rgb(233, 233, 233);
}

.show {
  display: flex;
}

@media only screen and (max-width:1000px) {
  .topbarr-container {
    width: 1000px;
  }
}

/* ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */

/* PRESENTATION */
/* ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */
.presentation-container {
  padding-top: 2rem;
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.presentation-container h1 {
  font-size: 5rem;
  text-align: center;
  font-weight: bolder;
  padding-bottom: 2rem;
}

.presentation-container p {
  width: 50rem;
  text-align: center;
  font-size: 1rem;
  font-weight: 600;
  color: rgb(110, 110, 110);
}

.presentation-btns {
  display: flex;
  flex-direction: row;
}

.presentation-container a {
  text-decoration: none;
  padding: 1rem 2rem;
  border-radius: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2rem;
  transition: .5s ease-in-out;
}

.presentation-container a:hover {
  border: 3px solid rgb(255, 255, 255);
}

.github {
  background-color: black;
  border: 3px solid rgb(00, 00, 00);
  color: rgb(255, 255, 255);
}

.linkedin {
  border: 3px solid #0a66c2;
  background-color: #0a66c2;
  color: rgb(255, 255, 255);
}

.presentation-container a>img {
  width: 1.5rem;
  height: 1.5rem;
  padding-right: 1rem;
}


@media only screen and (max-width:1000px) {
  .presentation-container {
    height: 50rem;
    width: 1000px;
  }

  .presentation-container h1 {
    font-size: 4rem;
  }

  .presentation-container p {
    font-size: 1.2rem;
  }
}

/* ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */

/* PROJECTS */
/* ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */
.projects-container {
  display: flex;
  flex-direction: column;
}

.projects-container h2 {
  font-size: 2rem;
  font-weight: bolder;
  text-align: center;
  padding-bottom: 2rem;
}

.projects-cards {
  display: grid;
  justify-content: space-around;
  row-gap: 2rem;
  grid-template-columns: repeat(auto-fill, 45rem);
  grid-auto-rows: minmax(10vw, auto);
}

.projects-cardElement {
  height: 35rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 0px 5px rgb(161, 161, 161);
  border-radius: 1rem;
  transition: .5s ease-in-out;
}

.projects-cardElement:hover {
  box-shadow: 0px 0px 80px rgb(161, 161, 161);
}

.projects-cardElement img {
  height: 20rem;
  border-radius: 1rem;
  margin-top: 1rem;
  cursor: pointer;
}

.cardElement-info {
  padding: 2rem;
  height: 15rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.cardElement-title-subtitle {
  padding-bottom: 1rem;
}

.cardElement-title-subtitle h3 {
  padding-bottom: 1rem;
}

.cardElement-tecs-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tecs {
  width: 20rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, 2rem);
  grid-auto-rows: minmax(1rem, 1rem);
  column-gap: 3rem;
  row-gap: 1.5rem;
}

.tecs p {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 2rem;
  font-weight: bold;
  border: 1px solid rgb(161, 161, 161);
  border-radius: 1.5rem;
}

.card-link {
  padding: 1rem 2rem;
  margin-left: 0.7rem;
  border-radius: 1.5rem;
  text-decoration: none;
  cursor: pointer;
  transition: .7s ease-in-out;
}

.git {
  background-color: rgb(00, 00, 00);
  border: 1px solid rgb(00, 00, 00);
  color: rgb(255, 255, 255);
}

.git:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(00, 00, 00);
}

.web {
  border: 1px solid rgb(0, 182, 91);
  background-color: rgb(0, 182, 91);
  color: rgb(255, 255, 255);
}

.web:hover {
  color: rgb(0, 182, 91);
  background-color: rgb(255, 255, 255);
}

@media only screen and (max-width:1000px) {

  .projects-container {
    width: 1000px;
  }

  .projects-cards {
    grid-template-columns: repeat(auto-fill, 30rem);
    grid-auto-rows: minmax(10rem, auto);
  }

  .projects-cardElement {
    height: 35rem;
  }

  .projects-cardElement img {
    height: 13rem;
  }

  .cardElement-info {
    width: 27rem;
  }

  .card-link {
    padding: .7rem 1.7rem;
  }
}

/* ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */

/* ABOUT */
/* ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */
.about-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 50rem;
}

.about-container h2 {
  font-size: 2rem;
  font-weight: bolder;
}

.about-info {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.about-text-skills {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 25rem;
  margin-right: 5rem;
}

.about-text {
  margin-top: 6rem;
  width: 50rem;
  font-size: 1.2rem;
  color: rgb(110, 110, 110);
}

.about-skills {
  padding-top: 2rem;
  height: 5rem;
}

.about-img-skills-cv {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-img-skills-cv img {
  width: 15rem;
  height: 15rem;
  border-radius: 50%;
  margin-bottom: 1rem;
}


.about-img-skills-cv a {
  padding: 1rem 3rem 1rem 2rem;
  background-color: rgb(00, 00, 00);
  border: 1px solid rgb(00, 00, 00);
  border-radius: 2rem;
  text-decoration: none;
  color: rgb(255, 255, 255);
  transition: .5s ease-in-out;
  display: flex;
  align-items: center;
  background-image: url("../public/images/arrow_down_white.svg");
  background-repeat: no-repeat;
  background-position: 90%;
}

.about-img-skills-cv a:hover {
  background-image: url("../public/images/arrow_down_black.svg");
  color: rgb(00, 00, 00);
  background-color: rgb(255, 255, 255);
}

.about-img-skills-cv a>img {
  width: 1.5rem;
  height: 1.5rem;
  padding-top: 15px;
  padding-left: 10px;
}


@media only screen and (max-width:1000px) {
  .about-container {
    width: 1000px;
    padding-bottom: 5rem;
  }

  .about-text {
    width: 30rem;
  }
}

/* ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */

/* FOOTER */
/* ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */

.footer-container {
  background-color: rgb(00, 00, 00);
  height: 15rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.footer-container a {
  text-decoration: none;
  color: rgb(110, 110, 110);
  display: flex;
  align-items: center;
  padding-bottom: 1rem;
  transition: .5s ease-in-out;
  padding: 0 1rem 0 1rem;
}

.footer-container a:hover {
  color: rgb(255, 255, 255);
}

.footer-nav,
.footer-links {
  display: flex;
  justify-content: center;
}

.footer-nav {
  padding-top: 1rem;
  width: 50%;
  font-size: 1.5rem;
}

.footer-links {
  width: 30%;
  height: 4rem;
}

.footer-links img {
  width: 2rem;
  height: 2rem;
  opacity: .5;

}

.footer-links img:hover {
  opacity: 1;
}

@media only screen and (max-width:1000px) {
  .footer-container {
    width: 1000px;

  }
}

/* ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */